body{
    background-color: #FFA3FD;
}

.task-manager{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
}

.tasks{
    width: 400px;
    background-color: #000d84;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 15px;
} 

.task{
    background-color: #865DFF;
    border-radius: 5px;
    padding: 1px;
    display: flex;
    align-items: center;
    gap: 10px;
}

button{
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    color: white;
    font-size: 13px;
    font-weight: bold;
    cursor: pointer;
}

.task span.content{
    flex-grow: 1;
    color: white;
}

.task input.edit-input{
    flex-grow: 1;
}

.task.completed{
    opacity: 0.5;
}

.edit{
    background-color: transparent;
}

.delete{
    background-color: red;
}

.save{
    background-color: green;
}

.add-task-container button{
    background-color: green;
}

input{
    border:1px solid green;
    border-radius: 5px;
    padding: 8px 12px;
}


.add-task-container{
    width: 400px;
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-top: 20px;
}

.add-task-container input{
    flex-grow: 1;
}